body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
    font-family: 'Quicksand', sans-serif;
}

body{
    overflow-x: hidden;
}
.InicioDisplay .parallax {
    background-image: url(/bonnito/static/media/mainPic.2830debd.png);
    min-height: 300px;
    background-attachment: unset;
    background-position: center;
    height: 600px;
    background-repeat: no-repeat;
    background-size: cover;
    max-height: 700px;
    display: -webkit-flex;
    display: flex;
    z-index: 2;
    -webkit-justify-content: center;
            justify-content: center;

}

.InicioDisplay .menuContainer{
    z-index: 1;
    margin: 0!important;
    border-bottom: none;
    text-align: right;
    background-color: white;
    box-shadow: 0px 5px 10px 0px lightgray;

}

.InicioDisplay #menu .ant-col-24{
    display: block;
    box-sizing: border-box;
    width: 100%;
    /*padding: 10px 0!important;*/
    /*height: 80px;*/
    text-align: right;
}
#menu .ant-menu-horizontal{
    line-height: 55px!important;
}
#menu .menuItem{
    height: 70px!important;
    vertical-align: middle;
    line-height: 65px;
}
.InicioDisplay .fixed-nav {
    border-bottom: none;
    margin: 0!important;
    position: fixed!important;
    top:0;
    background-color: white;
    box-shadow: 0px 5px 10px 0px lightgray;
    z-index: 1;
    width: 100%;

}

.InicioDisplay .menu{
    background-color: transparent;
    color: gray;
    border: none;
    font-weight: bold;
    font-size: 1.1rem;
    padding-right: 50px;
}
.InicioDisplay .ant-menu-item-active{
    color:gray!important;
    border-color: gray!important;
    background-color: #EBEBEB!important;
}
.InicioDisplay .ant-menu-item-selected{
    color:gray!important;
    border-color: gray!important;
}

.InicioDisplay .logo{
    position: absolute;
    top:6px;
    left: 50px;
    width:250px;
}

.InicioDisplay .title{
    text-align: center;
    font-size: 2.5rem;
    padding: 50px 0 0 0;
}

.InicioDisplay .history{

}

.InicioDisplay .historyDescription{
    margin:10px auto auto auto;
    text-align: justify;
    width:40%;
    padding: 30px 0;
    font-size: 1rem;
}

.InicioDisplay .about{
    background-color: #EBEBEB;
}

.InicioDisplay .aboutTitle{
    color: grey;
    padding: 0 25px 0 33px;
    font-weight: 700;
}

.InicioDisplay .linea, .lineaDerecha{
    opacity: 0.1;
}

.InicioDisplay .lineaDerecha{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.InicioDisplay .productsList{
    margin-top: 5%;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.InicioDisplay .productCard{
    margin:1% 30px 30px 30px!important;
    text-align: center;
    width:20vw !important;
    min-height: 400px !important;
    border:2px solid rgba(0, 0, 0, 0.473) !important;

}
.InicioDisplay .productCard .productImage{}

.ant-card-meta-title {
    text-align: center;
    white-space: normal !important;
    text-align: center !important;
    min-height: 43px !important;
    max-height: 43px !important;
}

.ant-card-head{
    min-height: 80px !important;
    max-height: 80px !important;
    border-radius: 1px !important;
    background-color: rgba(126, 126, 126, 0.85) !important;
    color:white !important;
}

.ant-card-head-title{
    white-space: normal !important;
    margin:0 !important;
    display: block !important;
    font-weight: bold !important;
}

.footer{
    background-color: gray;
    font-weight: bold;
    padding: 50px 0;
    height: 100px;
    text-align: center;
}

.footer a{
    color: white;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

